@import url('https://fonts.googleapis.com/css2?family=Anek+Tamil:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  font-family: 'Anek Tamil', sans-serif;
}
.title {
  text-align: center;
}
.form {
  &__btn {
    border: none;
  }
}
